import React, { useState } from "react";
import Preloader from "../../common/preloader/Preloader";
import s from './ProfileInfo.module.css';
import User from '../../../assets/images/user.png'
import ProfileData from "./ProfileData";
import editModeImg from "../../../assets/images/edit-profile.png"
import EditModeProfileDataReduxForm from "./EditModeProfileData";


const ProfileInfo = (props) => {
  let [inputBlockVision, setInputBlockVision] = useState(false)
  let [editMode, setEditMode] = useState(false)
  if (!props.profile) {
    return <Preloader />
  }
  const onMainPhotoSelect = (e) => {
    debugger
    if (e.target.files.length) {
      props.savePhoto(e.target.files[0], props.profile.id)
    }
  }
  const onSubmit = (formData) => {
    props.saveProfile(formData).then(() => {
      setEditMode(false)
    })
  }
  return (
    <div className={s.descriptionBlock}>
      <div className={s.leftBlock}>
        <div onMouseOver={() => { setInputBlockVision(true) }} onMouseLeave={() => { setInputBlockVision(false) }}>
          <div className={s.photo}>
            <img src={props.profile.photos.large || User}></img>
            <input type='file' id='file' onChange={onMainPhotoSelect}/>
            <label for='file'>New photo</label>
          </div>
          {/* <div className={s.photoInput}><input type='file' id='file' onChange={onMainPhotoSelect}/><label for='file'>New photo</label></div> */}
          {props.isOwner && <div className={s.editModeButton} onClick={() => { setEditMode(true) }}>
            <div className={s.editProfileBtn}>
              <img src={editModeImg} />
            <span>Edit Profile</span>
            </div>
          </div>
          }
        </div>
      </div>
      {editMode ?
        <EditModeProfileDataReduxForm initialValues={props.profile} profile={props.profile} onSubmit={onSubmit} />
        : <ProfileData {...props} />}
    </div>
  )
};

export default ProfileInfo;