import React from "react";
import s from './ProfileInfo.module.css';
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";



const ProfileData = (props) => {
  return (
    <div className={s.rightBlock}>
      <div className={s.pageTop}>
        <div className={s.name}>{props.profile.fullName}</div>
        <ProfileStatusWithHooks className={s.status} isOwner={props.isOwner} status={props.status} updateStatus={props.updateStatus} />
      </div>
      <div className={s.pageBottom}>
        <div className={s.item}>
          Looking for a job: {props.profile.lookingForAJob ? 'yes' : 'no'}
        </div>
        <div className={s.item}>
          My professional skill: {props.profile.lookingForAJobDescription}
        </div>
        <div className={s.item}>
          About me: {props.profile.aboutMe}
        </div>
        <div className={s.item}>
          Contacts: {Object.keys(props.profile.contacts).map(key => {
            return <Contacts key={key} contactsTitle={key} contactValue={props.profile.contacts[key]} />
          })}
        </div>
      </div>
    </div>
  )
}
const Contacts = ({ contactsTitle, contactValue }) => {
  return (
    <div className={s.contacts}>{contactsTitle}: {contactValue}</div>
  )
}

export default ProfileData;