import React from "react";
import { reduxForm } from "redux-form";
import { Field } from "redux-form";
import { Input, Textarea } from "../../common/FormsControls/FormsControls";
import s from './ProfileInfo.module.css';

const EditModeProfileData = ({ handleSubmit, profile, error }) => {
  return (
    <form className={s.rightBlock} onSubmit={handleSubmit}>
      <div className={s.pageTop + ' ' + s.rightBlock}>
        Full name:
        <Field component={Input} name={'fullName'} />
      </div>
      <div className={s.pageBottom}>
        <div>
          Looking for a job:
          <Field component={Input} type='checkbox' name={'lookingForAJob'} />
        </div>
        <div>
          My professional skill:
          <Field component={Textarea} name={'lookingForAJobDescription'} />
        </div>
        <div>
          About me:
          <Field component={Textarea} name={'aboutMe'} />
        </div>
        <div>
          Contacts: {Object.keys(profile.contacts).map(key => {
            return <div key={key} className={s.contacts}>
              {key}: <Field component={Input} name={'contacts.' + key} />
            </div>
          })}
        </div>
        {error && <div className={s.formSummaryError}>{error} </div>}
      </div>
      <div>
        <button>Save</button>
      </div>

    </form>
  )
}
const EditModeProfileDataReduxForm = reduxForm({ form: 'EditProfile' })(EditModeProfileData)

export default EditModeProfileDataReduxForm;